.footer-container{
    .block-footer-contacts{
        margin-bottom: 10px;
        clear: both;

        .content{
            overflow: hidden;
        }

        .operator-icon{
            float: left;
            margin: 0 10px 0 0;
        }
    }

    @include bp(max-width, 767px){
        .block-footer-contacts{
            .operator-icon{
                float: none;
            }

            .content{
                display: inline;
            }
        }
    }
}