#nav {
    .nav-panel-inner {
        padding: 23px 0;
    }

    .nav-panel--dropdown {
        min-height: 100%;
        box-sizing: border-box;
    }
}

.nav-regular {
    .classic {
        > .nav-panel--dropdown {
            padding: 20px 0;

            li{
                position: static;
                padding: 0 20px;
            }
        }
    }
    .nav-panel {
        li.classic {
            &:hover {
                > .nav-panel--dropdown {
                    left: 100%;
                }
            }
        }

        li.mega {
            &:hover {
                > .nav-panel--dropdown {
                    left: 100%;
                }
            }
        }
    }

    .nav-submenu--mega > li.classic:hover > .nav-panel--dropdown {
        left: 100%;
    }
}
