.header-container {
  .nav-regular {
    > .nav-item.first {
      position: relative;

      @include bp(min-width, 960px) {
        min-width: 270px;
        box-sizing: border-box;

        > a {
          padding-left: 22px;

          > .caret {
            margin-left: 15px;
          }
        }

        > ul {
          width: 100%;
        }
      }
    }
  }

  .right-column .dropdown.header-contacts {
    .dropdown-content {
      min-width: 230px;

      li {
        margin-bottom: 8px;
        white-space: normal;
        clear: both;
      }

      .operator-icon {
        float: left;
        margin: 0 8px 8px 0;
      }

      .content {
        display: block;
        overflow: hidden;
        margin-bottom: 8px;
      }
    }
  }
}
.mini-cart .actions button {
  font-size: 14px;
}
